.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible; }
  .hamburger:hover {
    opacity: 0.7; }

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px; }
  .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    width: 40px;
    height: 4px;
    background-color: #000;
    border-radius: 4px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  .hamburger-inner::before, .hamburger-inner::after {
    content: "";
    display: block; }
  .hamburger-inner::before {
    top: -10px; }
  .hamburger-inner::after {
    bottom: -10px; }

/*
   * Elastic
   */
.hamburger--elastic .hamburger-inner {
  top: 2px;
  transition-duration: 0.275s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55); }
  .hamburger--elastic .hamburger-inner::before {
    top: 10px;
    transition: opacity 0.125s 0.275s ease; }
  .hamburger--elastic .hamburger-inner::after {
    top: 20px;
    transition: transform 0.275s cubic-bezier(0.68, -0.55, 0.265, 1.55); }

.hamburger--elastic.is-active .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(135deg);
  transition-delay: 0.075s; }
  .hamburger--elastic.is-active .hamburger-inner::before {
    transition-delay: 0s;
    opacity: 0; }
  .hamburger--elastic.is-active .hamburger-inner::after {
    transform: translate3d(0, -20px, 0) rotate(-270deg);
    transition-delay: 0.075s; }

.main-nav .bounce {
  animation-duration: 3s;
  animation-delay: 1s; }

.main-nav .fadeInLeft {
  animation-duration: 3s;
  animation-delay: 2s; }

.main-nav-top-section, .main-nav-toggle-button, .main-nav-bottom-section {
  position: fixed;
  z-index: 20; }
  .main-nav-top-section__button, .main-nav-toggle-button__button, .main-nav-bottom-section__button {
    background: none;
    border: none;
    cursor: pointer; }
    .main-nav-top-section__button svg, .main-nav-toggle-button__button svg, .main-nav-bottom-section__button svg {
      fill: #fff;
      height: 30px; }

.main-nav-top-section, .main-nav-bottom-section {
  background-color: #1C262B;
  height: 73px;
  width: 100%; }

.main-nav-top-section {
  align-items: center;
  display: grid;
  grid-template-columns: 90px 1fr 90px;
  grid-template-rows: 80px;
  justify-items: center;
  top: 0;
  transition: top 0.5s; }
  .main-nav-top-section--hide {
    top: -100px; }
  .main-nav-top-section__image {
    cursor: pointer;
    height: auto;
    margin-left: 52px;
    width: 124px; }
  .main-nav-top-section .main-nav-search-form {
    position: relative; }
    .main-nav-top-section .main-nav-search-form__input {
      border: none;
      border-radius: 20px;
      outline: none;
      overflow: hidden; }
    .main-nav-top-section .main-nav-search-form__button {
      background: #b446ff;
      border: none;
      border-radius: 20px;
      cursor: pointer;
      height: 35px;
      left: 125px;
      outline: none;
      position: absolute;
      top: 0;
      width: 60px; }
      .main-nav-top-section .main-nav-search-form__button svg {
        fill: #fff;
        height: 30px; }

.main-nav-toggle-button {
  right: 0;
  outline: none;
  top: 12px; }
  .main-nav-toggle-button .hamburger-inner,
  .main-nav-toggle-button .hamburger-inner:before,
  .main-nav-toggle-button .hamburger-inner:after {
    background-color: #fff; }

.main-nav-bottom-section {
  align-items: center;
  bottom: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  height: 68px;
  justify-items: center;
  transition: bottom 0.5s; }
  .main-nav-bottom-section .ticket-none {
    display: none; }
  .main-nav-bottom-section__button svg {
    height: 35px; }
  .main-nav-bottom-section--hide {
    bottom: -100px; }

